@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@100;300;400;500;600;700;800;900&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
}

html {
  height: 100%;
  background-color: #0a0a0a;
  /* scroll-behavior: smooth; */
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: rgb(211, 211, 211);
  border-radius: 10px;
  box-shadow: inset 3px 3px 5px rgba(128, 128, 128, 0.425);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(167, 167, 167);
  box-shadow: inset -3px -3px 5px rgba(0, 0, 0, 0.274);
}
body {
  --c1: #be2600;
  --c1h: #e40000;
  --c1t: #94250983;
  --drakBg: #0a0a0a;
  max-height: 0;
  overflow-x: hidden;
  background-color: #0a0a0a;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a, svg, button, input, li {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
